import React, { useState } from "react";
import classes from "./Filter.module.css";

const Filter = (props) => {
  const filterItems = [
    { name: "А", id: 1 },
    { name: "Б", id: 2 },
    { name: "В", id: 3 },
    { name: "Г", id: 4 },
    { name: "Д", id: 5 },
    { name: "Е", id: 6 },
    { name: "Ё", id: 7 },
    { name: "Ж", id: 8 },
    { name: "З", id: 9 },
    { name: "И", id: 10 },
    { name: "К", id: 11 },
    { name: "Л", id: 12 },
    { name: "М", id: 13 },
    { name: "Н", id: 14 },
    { name: "О", id: 15 },
    { name: "П", id: 16 },
    { name: "Р", id: 17 },
    { name: "С", id: 18 },
    { name: "Т", id: 19 },
    { name: "У", id: 20 },
    { name: "Ф", id: 21 },
    { name: "Х", id: 22 },
    { name: "Ц", id: 23 },
    { name: "Ч", id: 24 },
    { name: "Ш", id: 25 },
    { name: "Щ", id: 26 },
    { name: "Э", id: 27 },
    { name: "Ю", id: 28 },
    { name: "Я", id: 29 },
  ];

  const [activeFilterItem, setActiveFilterItem] = useState({
    active: false,
    itemId: null,
  });

  const onFilterItemClick = (event) => {
    setActiveFilterItem({ active: true, itemId: event.target.id });

    props.onFilterClick(event.target.innerHTML);
  };

  return (
    <ul className={classes.region_search__filter}>
      {filterItems.map((item) => (
        <li
          key={item.id}
          id={item.id}
          className={
            activeFilterItem.active && activeFilterItem.itemId == item.id
              ? classes.focus
              : ""
          }
          onClick={onFilterItemClick}
        >
          {item.name}
        </li>
      ))}
    </ul>
  );
};

export default Filter;
